@media only screen and (min-width:600px) {
  .grad_photo {
    max-width: 30%;
    width: 100%;
    height: auto;
    border: 4px solid #272727;
    border-radius: 10px;
    position: absolute;
    z-index: 0;
  }

  .homepagetitle {

    font-family: Arial, Helvetica, sans-serif;
    font-size: 45px;
    background-image: linear-gradient(to top, #ffffff, #e3e3e3, #ffffff);
    color: transparent;
    background-clip: text;
    -webkit-background-clip: text;
    -webkit-text-stroke-width: 1.5px;
    -webkit-text-stroke-color: black;

  }

  .home-container {
    width: 100%;
    display: flex;
    overflow: auto;
    min-height: 100vh;
    align-items: center;
    flex-direction: row;
    justify-content: center;
  }

  .home-text {
    top: 22%;
    left: 15%;
    position: absolute;
    font-family: Arial, Helvetica, sans-serif;
    font-weight: bold;
    color: white;
    font-size: 2vw;
    z-index: 1;

  }

  .home-text1 {
    top: 32%;
    left: 15%;
    position: absolute;
    font-family: Arial, Helvetica, sans-serif;
    font-weight: bold;
    color: white;
    font-size: 2vw;
    z-index: 1;

  }

  .home-text2 {
    top: 27%;
    left: 27%;
    color: #e025c1;
    position: absolute;
    font-family: Arial, Helvetica, sans-serif;
    font-weight: bold;
    font-size: 2vw;
    z-index: 1;
    -webkit-text-stroke-width: 0.05vw;
    -webkit-text-stroke-color: white;
  }

  .home-text3 {
    top: 27%;
    left: 15%;
    position: absolute;
    font-family: Arial, Helvetica, sans-serif;
    font-weight: bold;
    color: white;
    font-size: 2vw;
    z-index: 1;
  }

  
}

